import { Component, OnInit } from '@angular/core';

import { NavController } from "@ionic/angular"
import { AuthService } from "./../../services/auth.service";
import {ErrorService} from "./../../services/error.service";

@Component({
  selector: 'admin',
  templateUrl: './admin.page.html',
  styleUrls: ['./admin.page.scss'],
})
export class AdminPage implements OnInit {

  

  title = "Admin";
  userCount = 0;
  userSessionCount = 0;
  displaySpinner: boolean = false;
  constructor(
    private auth: AuthService,
    private navController: NavController,
    private errorService: ErrorService
    ) {


  }

  getUserCount(displayMainSpinner: Boolean, refresher: any) {
    if (displayMainSpinner) {
        this.displaySpinner = true;
    }

    console.log("getting user count");

}

getUserSessionCount(displayMainSpinner: Boolean, refresher: any) {
    if (displayMainSpinner) {
        this.displaySpinner = true;
    }

    console.log("getting user session count");
    // this.loader.present();

}

refresh(refresher) {
    this.getUserSessionCount(false, refresher);
    this.getUserCount(false, refresher);
}

ngAfterViewInit() {
    this.getUserSessionCount(true, null);
    this.getUserCount(true, null);
    console.log("my adverts page loading");
}

onLink(url: string) {
    window.open(url);
}

  ngOnInit() {
    
  }

}
